.carta{
  background-color: transparent;
  perspective: 1000px;
}
.cartaInterior{
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.5s ease-in-out;
  transform-style: preserve-3d;
  background-color: #2b2416;
}
.volteaCarta{
  transform: rotateY(180deg);
}
.cartaFrontal, .cartaTrasera {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
  
}

.cartaFrontal {
  background-color: #bbb;
  color: black;
}

.cartaTrasera {
  background-color: #4E4F41;
  color: white;
  border: 3px  dotted  #32322A;
  transform: rotateY(180deg);
  
}
.parejaEncontrada{
  filter: sepia(100%);
  transition: 1s filter ease-in;
}

.borde{
  border: 10px solid transparent;
  -moz-border-image: url("../public/images/borde1.jpeg") 30 30 repeat; /* Old firefox */
  -webkit-border-image: url("../public/images/borde1.jpeg") 30 30 repeat; /* Safari */
  -o-border-image: url("../public/images/borde1.jpeg") 30 30 repeat; /* Opera */
  border-image: url("../public/images/borde1.jpeg") 30 30 repeat;
}
.waviy {
  position: relative;
}
.waviy span {
  position: relative;
  display: inline-block;
  color: #ca8a04;
  text-transform: uppercase;
  animation: waviy 2s infinite;
  animation-delay: calc(.1s * var(--i));
  -webkit-text-stroke:1px #2b2416;
}
@keyframes waviy {
  0%,40%,100% {
    transform: translateY(0)
  }
  20% {
    transform: translateY(-20px)
  }
}